@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --background: 0 0% 100%;
    --foreground: 0 0% 3.9%;
    --navbar-text: 0 0% 100%/0.55;
    --navbar-text-hover: 0 0% 100%/0.35;
    --navbar-background: 209 20% 17%;
    --navbar-icon: 0 0% 98%;
    --navbar-icon-count: 28 74% 50%;
    --card: 0 0% 100%;
    --card-foreground: 0 0% 3.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 0 0% 3.9%;
    --primary: 0 0% 9%;
    --primary-foreground: 0 0% 98%;
    --secondary: 0 0% 96.1%;
    --secondary-foreground: 0 0% 9%;
    --muted: 0 0% 96.1%;
    --muted-foreground: 0 0% 45.1%;
    --accent: 0 0% 96.1%;
    --accent-foreground: 0 0% 9%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 98%;
    --border: 0 0% 89.8%;
    --input: 0 0% 89.8%;
    --ring: 0 0% 3.9%;
    --radius: 0.5rem;
    --chart-1: 12 76% 61%;
    --chart-2: 173 58% 39%;
    --chart-3: 197 37% 24%;
    --chart-4: 43 74% 66%;
    --chart-5: 27 87% 67%;
  }

  .dark {
    --background: 0 0% 3.9%;
    --foreground: 0 0% 98%;
    --navbar-text: 210 40% 70%;
    --card: 0 0% 3.9%;
    --card-foreground: 0 0% 98%;
    --popover: 0 0% 3.9%;
    --popover-foreground: 0 0% 98%;
    --primary: 0 0% 98%;
    --primary-foreground: 0 0% 9%;
    --secondary: 0 0% 14.9%;
    --secondary-foreground: 0 0% 98%;
    --muted: 0 0% 14.9%;
    --muted-foreground: 0 0% 63.9%;
    --accent: 0 0% 14.9%;
    --accent-foreground: 0 0% 98%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 0 0% 98%;
    --border: 0 0% 14.9%;
    --input: 0 0% 14.9%;
    --ring: 0 0% 83.1%;
    --chart-1: 220 70% 50%;
    --chart-2: 160 60% 45%;
    --chart-3: 30 80% 55%;
    --chart-4: 280 65% 60%;
    --chart-5: 340 75% 55%;
  }

  h1 {
    @apply text-3xl font-bold;
  }
  h2 {
    @apply text-2xl font-semibold;
  }
  h3 {
    @apply text-xl font-medium;
  }
}

/*general styles start*/

HTML,
body {
    height: 100%;
    scroll-behavior: smooth;
}

#app {
    min-height: 100dvh;
    display: flex;
    flex-direction: column;
}

body {
    margin: 0;
    background-color: #fff;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.no-before::after,
.no-before::before {
    display: none;
}

.display-flex {
    display: flex;
}

button:hover {
    opacity: .8;
}

button:active {
    transform: translate(1px, 1px);
}

.hide {
    display: none;
}

.show {
    display: block;
}

.modal-dialog-centered {
    max-width: 550px;
}

.alert-button-self {
    background-color: #c44900;
}

.table__link {
    color: inherit;
    text-decoration: none;
}

.table__link:hover {
    color: inherit;
    text-decoration: underline;
}

/*general styles end*/

/*Spinner Start*/
.spinner {
    flex: 1 1 auto;
    width: 100vw;
    height: 77vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.spinner__shop {
    flex: 1 1 auto;
}

.td-spinner .spinner-border {
    width: 16px;
    height: 16px;
}

.spinner__device-info {
    width: 100%;
    height: 100%;
}

/*Spinner End*/

/*Error Page Start*/
.error__page {
    color: red;
    flex: 1 1 auto;
    font-size: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
}

/*Error Page End*/

.navbar .shop-search__search-icon{
    width: 60%;
    height: 60%;
}

/* auth start */
.auth {
    flex: 1 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.auth__form {
    font-weight: 500;
    background-color: #C4C4C4;
    padding: 10px 30px;
}

.auth__title {
    text-align: center;
    margin-bottom: 10px;
}

.auth__form input {
    width: 450px;
    background-color: transparent;
    display: block;
    border: 1px solid black;
    padding: 8px 16px;
    margin-bottom: 20px;
}

.auth__form input::placeholder {
    color: black
}

.auth__form input:first-of-type {
    margin-bottom: 8px;
}

.auth__submit-cont {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.auth__descr a {
    display: inline;
}

.auth__button {
    color: black;
    border: 1px solid black;
    background-color: transparent;
    font-weight: 500;
}

.btn-outline-light:hover {
    color: #000;
    background-color: darkgray;
    border-color: darkgray;
}

/* auth end */


/* shop start */
.shop {
    width: 100%;
    flex: 1 1 auto;
    padding: 8px 20px;
}

.shop__container {
    display: flex;
}

.shop__devices-cont {
    flex: 0 1 84%;
    margin-left: 20px;
}


/* shop end */
/*about start*/
.about {
    flex: 1 1 auto;
    margin: 60px 0;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
}

.about__title {
    font-size: 63px;
    font-weight: 700;
    text-align: center;
    margin: 35px auto 15px;
}

.about__descr {
    font-size: 19px;
    text-align: center;
    margin-bottom: 30px;
}

.about__cards {
    margin-bottom: 35px;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.about-card {
    padding: 8px;
    width: 28vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.about-card__img-cont {
    margin-bottom: 30px;
}

.about-card__img-cont img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.about-card__title {
    font-size: 35px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 30px;
}

.about-card__descr {
    font-size: 19px;
    text-align: center;
    margin-bottom: 30px;
}

.about-card__button {
    text-align: center;
    max-width: 312px;
    display: block;
    letter-spacing: 1.2px;
    margin: 0 auto 30px;
    text-decoration: none;
    font-weight: 500;
    font-size: 20px;
    padding: 20px 30px;
    border: 4px solid #212529;
    color: #212529;
    text-transform: uppercase;
}

.about-card__button:hover,
.about-card__button:focus,
.about-card__button:active {
    color: white;
    background-color: #212529;
    transition: all .3s ease-out;
}

/**about subpages**/
.sub-about {
    flex: 1 1 auto;
    margin: 60px 0;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    line-height: 1.2;
    background-color: white;
}

.sub-about__title {
    font-size: 63px;
    font-weight: 700;
    text-align: center;
    margin: 35px auto;
    max-width: 70vw;
}

.sub-about__descr {
    font-size: 19px;
    text-align: center;
    max-width: 70vw;
    margin: 0 auto 35px;
}

.experience-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.experience-block__img-cont {
    width: 100%;
}

.experience-block:nth-child(even) {
    flex-direction: row-reverse;
}

.experience-block:nth-child(even) .experience-block__title,
.experience-block:nth-child(even) .experience-block__descr {
    text-align: right;
}

.experience-block:nth-child(even) .experience-block__buttons {
    justify-content: flex-end;
}

.experience-block:nth-child(even) .experience-block__button {
    margin: 0 0 0 15px;
}

.experience-block__img-cont {
    flex: 0 1 45%;
    padding: 10px;
}

.experience-block__img-cont img {
    object-fit: cover;
    width: 100%;
    max-height: 500px;
}

.experience-block__descr-cont {
    flex: 0 1 45%;
    padding: 10px;
}

.sub-about__block {
    margin-bottom: 35px;
}

.experience-block__title {
    font-size: 35px;
    font-weight: 700;
    text-align: left;
    margin-bottom: 18px;
}

.experience-block__descr {
    font-size: 19px;
    text-align: left;
    margin-bottom: 28px;
}

.experience-block__buttons {
    display: flex;
    justify-content: flex-start;
}

.experience-block__button {
    margin: 0 15px 0 0;
}

/**who we are**/
.whoWe .experience-block {
    flex-direction: column;
    margin: 0 auto;
}

.whoWe .experience-block:last-child .experience-block__descr-cont .experience-block__title,
.whoWe .experience-block:last-child .experience-block__descr-cont .experience-block__descr {
    text-align: center;
}

.experience-block__img-small {
    display: none;
}

.whoWe .experience-block__img-cont {
    flex: 0 1 100%;
    max-width: 65vw;
    margin: 0 auto;
}

.whoWe .experience-block__descr-cont {
    flex: 0 1 100%;
    max-width: 60vw;
    margin: 0 auto;
}


.whoWe .experience-block:nth-child(even) .experience-block__title,
.whoWe .experience-block:nth-child(even) .experience-block__descr {
    text-align: left;
}

.experience-block:nth-child(even) .experience-block__buttons {
    justify-content: flex-start;
}

/*about end*/

/*protected start*/
.protected {
    flex: 1 1 auto;
}

.protected__container {
    margin: 60px auto;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    line-height: 1.2;
    background-color: white;
    padding: 0 12vw;
}

.protected__title {
    font-size: 63px;
    font-weight: 700;
    text-align: center;
    margin: 35px auto 15px;
    transition: all .3s ease-out;
}

.protected__descr {
    font-size: 19px;
    text-align: center;
    margin-bottom: 30px;
}

.protected__cards {
    margin-bottom: 35px;
    display: flex;
    justify-content: space-evenly;
}

.prot-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px;
    text-decoration: none;
    transition: all .3s ease-out;
}

.prot-card:nth-child(odd) {
    width: 15vw;
}

.prot-card:nth-child(even) {
    width: 30vw;
}

.prot-card__image-cont {
    width: 100%;
    height: 20vw;
    margin-bottom: 15px;
    transition: all .3s ease-out;
}

.prot-card__image-cont img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.prot-card__title {
    font-size: 21px;
    font-weight: 700;
    color: black;
    text-align: center;
    text-decoration: none;
    margin-bottom: 15px;
}

.prot-card__button-cont {
    width: 112px;
    height: 40px;
}

.prot-card__button-cont img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.prot-card__img-dark {
    display: none;
}

.prot-card:hover {
    transition: all .3s ease-out;
}

.prot-card:hover .prot-card__img {
    display: none;
    transition: all .3s ease-out;
}

.prot-card:hover .prot-card__img-dark {
    display: block;
    transition: all .3s ease-out;
}

.protected__cards:hover .prot-card:not(:hover) .prot-card__image-cont,
.protected__cards:hover .prot-card:not(:hover) .prot-card__title {
    opacity: .1;
    transition: all .3s ease-out;
}

/*protected end*/
/*help start*/
.help {
    flex: 1 1 auto;
}

.help__container {
    margin: 60px auto;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    line-height: 1.2;
    background-color: white;
    padding: 4vw;
}

.help__container>.spinner {
    height: 65vh;
}

.help__banner {
    margin-bottom: 30px;
    position: relative;
}

.help__banner::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: '';
    background-color: rgba(0, 0, 0, .3);
}

.help-banner {
    width: 100%;
    height: 30vh;
    background: center/cover no-repeat;
}

.help-banner h3 {
    font-size: 54px;
    color: white;
    font-weight: 700;
    text-align: center;
    z-index: 9;
    position: relative;
    padding-top: 4vh;
    margin-bottom: 4vh;
}

.help-banner__container {
    position: relative;
    z-index: 9;
    width: 500px;
    height: 30px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
}

.help-banner__container input {
    width: 100%;
    height: 40px;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
    border: none;
    padding: 0 12px;
}

.help-banner__container button {
    width: 60px;
    height: 40px;
    border-bottom-right-radius: 25px;
    border-top-right-radius: 25px;
    border: none;
    background: white url('./assets/icons/search.svg') 21px 11px;
}

.help__faq-cont {
    margin-bottom: 30px;
}

.faq-cont__title {
    color: #2d2d2d;
    font-size: 31px;
    font-weight: 700;
    letter-spacing: 2px;
    margin-left: 30px;
    margin-bottom: 20px;
}

.faq-card__container {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.faq-card {
    background-color: #f3f3f3;
    flex: 0 1 31%;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.faq-card__title {
    display: flex;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid white;
}

.faq-card__title a {
    font-weight: 500;
    font-size: 22px;
    margin-left: 10px;
    color: #2d2d2d;
    text-decoration: none;
}

.faq-card__title a:hover {
    text-decoration: underline;
}

.faq-card__links {
    padding: 10px;
}

.faq-card__links>.spinner {
    width: auto;
    height: auto;
}

.faq-card__links li {
    padding: 12px;
}

.faq-card__links li a {
    color: black;
    text-decoration: none;
}

.faq-card__all {
    padding: 8px;
    margin-left: 10px;
    margin-bottom: 15px;
}

.faq-card__all a {
    font-size: 20px;
    color: #2d2d2d;
    font-weight: 500;
    text-decoration: none;
}

.faq-card__links li a:hover,
.faq-card__all a:hover {
    text-decoration: underline;
}

.help__bottom-cont {
    display: flex;
    justify-content: space-evenly;
}

.popular-cont,
.help__contact {
    flex: 0 1 48%;
}

.popular-cont>h3,
.help__contact>h3 {
    color: #2d2d2d;
    font-size: 31px;
    font-weight: 700;
    letter-spacing: 2px;
    margin-left: 30px;
    margin-bottom: 20px;
    text-transform: uppercase;
}

.popular-cont__cards {
    background-color: #F3F3F3;
}

.popular-cont__cards {
    padding: 0 10px;
}

.popular-cont__cards li {
    padding: 12px;
}

.popular-cont__cards li:not(:last-child) {
    border-bottom: 1px solid white;
}

.popular-cont__cards li a {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: black;
    text-decoration: none;
}

.popular-cont__cards li a:hover {
    text-decoration: underline;
}

.help__contact-cont {
    width: 100%;
    height: auto;
    background-color: #f3f3f3;

}

.contact-cont__banner {
    width: 100%;
    height: 25vh;
    margin-bottom: 20px;
}

.contact-cont__banner img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.contact-cont__button-wrapper {
    text-align: center;
}

.contact-cont__button-wrapper a:hover,
.contact-cont__button-wrapper button:hover {
    background-color: white;
    transition: all .3s ease-out;
}

.contact-cont__button-wrapper button:disabled {
    background: white;
    opacity: .3;
}

.contact-cont__button-wrapper a,
.contact-cont__button-wrapper button {
    display: inline-block;
    color: #2d2d2d;
    font-size: 27px;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: none;
    border: 1px solid black;
    padding: 10px 20px;
    margin: 20px;
    transition: all .3s ease-out;
}

.help__cat-list .spinner-border {
    margin: 0 auto;
    display: block;
    margin-bottom: 20px;
}

.help-faq__main-cont {
    background-color: #F3F3F3;
    padding: 8px;
    line-height: 1.5;
    margin-bottom: 10px;
    font: 16px / 26px system-ui, Roboto, Arial, sans-serif;
}

.help-faq__main-body p {
    text-indent: 20px;
    margin-bottom: 8px;

}

.related-cont h3,
.help-faq__main-cont h3 {
    font-size: 19px;
}

.related-cont h3 {
    margin: 0 30px 20px 30px;
    padding-top: 20px;
}

.help-faq__main-cont h3 {
    text-align: center;
    color: #2d2d2d;
    font-weight: 500;
    letter-spacing: 1.5px;
    margin-bottom: 10px;
}

.help-faq__main-cont h4 {
    font-size: 16px;
    text-align: center;
    color: #767676;
    font-weight: 500;
    letter-spacing: 1.5px;
    margin: 10px 0;
    text-transform: uppercase;
}

.related-cont {
    background-color: #fff;
}

/**help breadcrumbs**/
.help__breadcrumbs {
    border-bottom: 1px solid #ddd;
    position: relative;
    top: -40px;
    padding-bottom: 20px;
    text-transform: lowercase;
}

.help__breadcrumbs a {
    font-size: 13px;
    color: black;
    text-decoration: none;
}

.help__breadcrumbs a:hover,
.help__breadcrumbs a:focus {
    text-decoration: underline;
}

.help__breadcrumbs .active {
    color: #2d2d2d;
}

.breadcrumbs-arrow,
.breadcrumbs-active {
    font-size: 13px;
    color: black;
    color: #2d2d2d;
}

.breadcrumbs-active {
    text-decoration: underline;
}

/**help subpage**/
.aside-h3 {
    color: #2d2d2d;
    font-size: 22px;
    font-weight: 700;
    padding: 13px 0;
    text-transform: uppercase;
}

.help__main-wrapper {
    display: flex;
}

.help__category {
    flex: 0 1 70%;
    background-color: #f3f3f3;
}

.help__category>.spinner {
    width: auto;
}

.help__error .error__page {
    margin-top: 10dvh;
}

.help__aside {
    flex: 0 1 30%;
    margin-left: 20px;
}

.help-aside__topics {
    background-color: #f3f3f3;
    padding: 0 20px;
    margin-bottom: 10px;
}

.help-aside__search {
    background-color: #f3f3f3;
    padding: 0 20px;
    margin-bottom: 10px;
}

.help-aside__contact {
    background-color: #f3f3f3;
    margin-bottom: 10px;
}

.help__faq {
    background-color: white;
}

/**help subpage banner**/
.help__cat-banner {
    width: 100%;
    height: 20vh;
    position: relative;
}

.help__cat-banner img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.help__cat-banner::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: '';
    background-color: rgba(0, 0, 0, .3);
}

.help__cat-banner h3 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: .7;
    font-size: 34px;
    color: white;
    font-weight: 700;
    text-align: center;
}

/**help categories list**/
.cat-list li:first-child {
    color: #2d2d2d;
    font-weight: 500;
}

/**help progress bar**/
.cat-list__progress {
    max-width: 300px;
    margin: 0 auto;
    text-align: center;
}


/**help topics aside**/
.topic-aside li {
    display: flex;
    align-items: center;
    padding: 8px 0;
}

.topic-aside li:not(:last-child) {
    border-bottom: 1px solid white;
}

.topic-aside li a {
    width: 100%;
    margin-left: 8px;
    text-decoration: none;
    color: black;
}

.topic-aside li a:hover {
    text-decoration: underline;
}

.topic-aside__icon {
    width: 50px;
    height: 50px;
}

.topic-aside__icon img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

/**help aside search**/
.aside-search__wrapper {
    position: relative;
    padding-bottom: 20px;
}

.aside-search__wrapper input {
    width: 100%;
    height: 50px;
    border: 1px solid black;
    padding-left: 12px;
}

.aside-search__wrapper button {
    position: absolute;
    top: 0;
    right: 0;
    width: 70px;
    height: 50px;
    border: none;
    background-color: white;
    background: url('./assets/icons/search.svg') no-repeat 30px 15px;
}

/**help aside search**/
.help-aside__contact h3 {
    margin-left: 20px;
}

.contact-cont__button-wrapper a {
    margin: 0 20px 20px;
}

/*help end*/

/*typebar start*/
.typebar {
    flex: 0 1 16%;
    margin-top: 90px;
    height: fit-content;
