.about-arrow::before {
    position: absolute;
    top: 0;
    left: 15px;
    width: 30px;
    height: 30px;
    transform: rotate(45deg);
    content: '';
    border-bottom: 1px solid black;
    transition: all .3s ease-in-out;
}

.about-arrow::after {
    position: absolute;
    top: 0;
    right: 15px;
    width: 30px;
    height: 30px;
    transform: rotate(-45deg);
    content: '';
    border-bottom: 1px solid black;
    transition: all .3s ease-in-out;
}

.about-arrowDown::before {
    position: absolute;
    top: 19px;
    left: 15px;
    width: 30px;
    height: 30px;
    transform: rotate(135deg);
    content: '';
    border-bottom: 1px solid black;
    transition: all .3s ease-in-out;
}

.about-arrowDown::after {
    position: absolute;
    top: 19px;
    right: 15px;
    width: 30px;
    height: 30px;
    transform: rotate(-135deg);
    content: '';
